import React from 'react';
import './AdminuserMG.css'
import axios from 'axios';
import socket from '../../helpers/sockets';

class AdminuserMG extends React.Component{
    constructor(props){
        super(props);
        this.state={
            option: 'new',
            password: this.props.password,
            toDeleteID:0,
            toCreate : {
                name : '',
                code: '',
                quote: '',
                minutes:'',
                image:null,
                fileext:'',
                socketid: socket.id
            }
        };
    }
    
    deleteuser = () => {
        axios.post("http://192.168.1.69:4000/modify",{ 
                    socketid: socket.id,
                    toDeleteID : parseInt(this.state.toDeleteID),
                    adminpass: this.state.password,
                    mode : "delete"
                });
    }
    ChangeValueInState = (event) =>{
       switch (event.target.id) {
           case "toDeleteID":
            this.setState({toDeleteID : event.target.value});
               break;
            case "createUserName":
                this.state.toCreate.name = event.target.value;
                this.setState(this.state);
                break;
            case "createUserQuoute":
                this.state.toCreate.quote = event.target.value;
                this.setState(this.state);
                break;
            case "createUserMinutes":
                this.state.toCreate.minutes = event.target.value;
                this.setState(this.state);
                break;
            case "createUserCode":
                this.state.toCreate.code = event.target.value;
                this.setState(this.state);
                break;
            case "createUserPicture":
                this.state.toCreate.image = event.target.files[0];
                this.state.toCreate.fileext = "." + ( event.target.files[0].type.split("/")[1]);
                this.setState(this.state);
                break;
       
           default:
               break;
       }
    }
    CreateUser = () =>{
        const url = 'http://192.168.1.69:4000/upload';
                const formData = new FormData();
                formData.append('avatar',this.state.toCreate.image);
                formData.append('code',this.state.toCreate.code);
                formData.append('name',this.state.toCreate.name);
                formData.append('quote',this.state.toCreate.quote);
                formData.append('minutes',this.state.toCreate.minutes);
                formData.append('fileext',this.state.toCreate.fileext);
                formData.append('socketid',socket.id);

                const config = {
                    headers: {
                        'content-type': 'multipart/form-data'
                    }
                }
                axios.post(url, formData,config)
    }
    ResolveState = () =>{
        switch (this.state.option) {
            case 'new':
               return (
                <div className="createform">
                <h6>Felhasználó létrehozás</h6>
                
                    <form></form>
                <input  onChange={this.ChangeValueInState} type="text" id="createUserName" name="createUserName" placeholder="Felhasználó neve"/>
                <input onChange={this.ChangeValueInState} type="text" id="createUserQuoute" name="createUserQuoute" placeholder="Felhasználó idézete"/>
                <input onChange={this.ChangeValueInState} type="number" id="createUserMinutes" name="createUserMinutes" placeholder="Ledolgozott percek"/>
                <input onChange={this.ChangeValueInState} type="number" id="createUserCode" name="createUserCode" placeholder="kód"/>
                <div className="profliePictureDiv"><p>Profilkép</p>
                <input onChange={this.ChangeValueInState} type="file" id="createUserPicture" name="createUserPicture" placeholder="Profilkép"/></div>
                
                <button onClick={this.CreateUser}>létrehozás</button>
                 </div>)
                
                case 'delete':
                    return   (
                        <div className="deleteform">
                            <h6>Felhasználó törlés</h6>
                            <input onChange={this.ChangeValueInState} type="number" id="toDeleteID" name="toDeleteID" placeholder="kód"/>
                            <button onClick={this.deleteuser}>Törlés</button>
                        </div>
                        
                        )
                   
            default:
                return    <h1>new</h1>
                
        }
    }

    render(){
        return(
            <div className="mainsolver">
                <div className="Micronav">
                    <button onClick={(event)=>{
                        this.setState({option:event.target.id})
                    }} id='new'>Új</button>
                    <button   onClick={(event)=>{
                        this.setState({option:event.target.id})
                    }} id='delete'>Törlés</button>
                </div>
                    <this.ResolveState/>
            </div>
        )
    }
}
export default AdminuserMG;