import React from 'react';


const statusCard = (status)=> {
    switch (status) {
        case 0:
         return(
             <div className="u-container-layout u-valign-middle u-container-layout-4" style={{backgroundColor: "grey"}}>
             <h4 className="u-text u-text-default u-text-2">Nem elérhető</h4>
               </div>
         )
            break;
            case 1:
             return(
                 <div className="u-container-layout u-valign-middle u-container-layout-4" style={{backgroundColor: "green"}}>
                 <h4 className="u-text u-text-default u-text-2">Elérhető</h4>
                   </div>
             )
            break;
            case 2:
             return(
                 <div className="u-container-layout u-valign-middle u-container-layout-4" style={{backgroundColor: "red"}}>
                 <h4 className="u-text u-text-default u-text-2">Vonalban</h4>
                   </div>
             )
            break;
        default:
         return(
             <div className="u-container-layout u-valign-middle u-container-layout-4" style={{backgroundColor: "grey"}}>
             <h4 className="u-text u-text-default u-text-2">Nem elérhető</h4>
               </div>
         )
            break;
    }
 }
class Usercard extends React.Component {

    constructor(props) { 
        super(props);
        this.state = props;
       
      }
    componentDidUpdate(prevProps) {
        if (prevProps !== this.props) {
          this.setState(this.props);
        }
      } 

    render() {
        
            return(
                <div classNameName="u-container-layout u-valign-top u-container-layout-1" style={{display:'flex',justifyContent:'center',maxWidth: 'max-content'}}>
                    <div className="u-align-center-xs u-container-style u-expanded-width-xs u-group u-group-1" style={{margin:'20px',height: "max-content"}}>
                      <div className="u-container-layout u-valign-bottom-xs u-container-layout-2" style={{padding:'0px',height: "max-content"}}>
                        <div className="u-container-style u-expanded-width u-group u-image u-image-1" data-image-width="1280" data-image-height="941" style={{backgroundImage: `url("${this.state.udata.avatar}")`}}>
                          <div className="u-container-layout u-container-layout-3">
                            <div className="u-align-center u-container-style u-custom-color-3 u-group u-shape-rectangle u-group-3">
                            {statusCard(this.state.udata.status)}
                             
                            </div>
                          </div>
                        </div>
                        <h3 className="u-align-left-lg u-align-left-md u-align-left-sm u-align-left-xl u-text u-text-default u-text-3">{this.state.udata.name}</h3>
                        <blockquote className="u-align-center-lg u-align-center-md u-align-center-sm u-align-center-xl u-text u-text-default u-text-4" style={{width: "100%",alignSelf: "flex-end !important"}}>"{this.state.udata.quote}"</blockquote>
                        <h4 className="u-align-left-lg u-align-left-md u-align-left-sm u-align-left-xl u-text u-text-default u-text-5">+36900000000</h4>
                        <h4 className="u-align-left-lg u-align-left-md u-align-left-sm u-align-left-xl u-text u-text-custom-color-3 u-text-default u-text-6">499 Ft/Perc</h4>
                        <h3 className="u-align-left-lg u-align-left-md u-align-left-sm u-align-left-xl u-text u-text-7">Kód: {this.state.udata.code}</h3>
                      </div>
                    </div>
                  </div>
            )
        
    
    }
  }
export default Usercard;

